            </a>
            <div class="apply_from">
                <div class="apply_boxs">
                    <h1 class="yw"><i>*</i>/</h1>
                    <div class="sun_input">
                        <input type="text" name="" v-model='full_name' placeholder="" class="full_name texts boders">
                    </div>
                    <h1 class="yw"><i>*</i></h1>
                    <div class="sun_input">
                        <input type="text" name="" class="texts Contacts boders" v-model='linkman' placeholder="">
                        <div class="err" v-if='errmsg==2'>
                            <i class="erricon" @click='errmsg = 0'></i>
                            <span></span>
                        </div>
                    </div>
                    <h1 class="yw"><i>*</i></br>
                    </h1>
                    <div class="sun_input" style="margin-bottom: .3rem;">
                        <input type="text" name="" class="texts Contacts boders" v-bind:class="{ success_phone: success_phone }"  v-model='mobile_no' placeholder=""  :disabled="success_phone">
                    </div>
                     <div class="sun_input" style="margin-bottom: 0;">
                       <input type="tel" pattern="[0-9]*" v-model="sms" placeholder=""  class="texts Contacts boders Contacts_init">
