                        <p>100</p>
                        <h2></h2>
                        <p>1.   </br>2. /</br>3.    DAPP</br>4.    </br>5.    </br>6.  NAST</p>
                        <h2></h2>
                        <p>1.   NAST<br>2.  C2C</p>
                        <h2></h2>
                        <p>1.    NAST30%30%40%1<br>2.   NAST365<br>3. NAST<br>4.    NAST</p>
                        <h2></h2>
                        <p>1.    NASTNAST<br>2.    <br>3.    NAST<br>4.    <br>5.  </p>
                        </div>
                </div>
            <div class="footer">
                  2018   
                  <p >ICP18030575-1</p> 
            </div> 
            <err-tip :tip_content='errtip' :tip_statie='tip_show'></err-tip>   
        </div>
    </div>
</template>
<script  type="text/javascript" charset="utf-8" >
import API from '../../api/api';  
import md5 from 'md5';
import utils from '@/assets/js/utils';
import errTip from '../../components/operation_tips'
export default{
     components:{
        errTip,
    },
    data(){
        return {
            success_phone:false,//
            errtip:'',//
            tip_show:false, //
            sms:'',
            retime:null,
            // 
            smsTime:60,
            // 
            timeout: false,
            checkphone:false,
            reaffirm:false,//
            thum_qiniu:"",
            qiniuli:'',
            qiniubaseUrl:API.imgurls,
            action:API.qinniu,//
            token:'',//token
            maximg:10,
            upimg:false,
            err:'',
            success:false,
            loaidng:',!',
            show_other:false,
            laoding:false,
            errmsg:0,
            full_name:'',//
            linkman:'',//
            wechat_id:'',//
            mobile_no:'',//
            business:[],//
            others:'',//
            srcimg:['license_img','idnumbe_just','idnumbe_back','logo_img'],
            imgs: {
                'license_img':'','idnumbe_just':'','idnumbe_back':'','logo_img':''
            }, 
            old_business:'',
        }
    },
    mounted(){
        this.getToken();
        this.businessData();
        
    },
    created(){
       
    },
    methods:{
        reGetTimeout(){
            var self = this;
            if(self.smsTime >1){
                self.smsTime --;
                this.retime = setTimeout(()=>{
                    self.reGetTimeout();
                },1000)
            }else{
                self.smsTime = 60;
                self.timeout = false;
                clearTimeout(self.retime);
            }
        },
        getSms(){           
            //
            let self = this;
            var phoneReg = /(^1\d{10}$)/;
            console.log(self.mobile_no)
            if(!phoneReg.test(self.mobile_no)){
                this.errfn('')
                return;
            }else {
                self.$axios.get(API.host+"/user/apply/check?mobile="+self.mobile_no).then(res=>{
                    console.log(res.data);
                    if(res.data.code=='200'){
                        this.checkphone=true;
                        this.success_phone =true;//
                        this.timeout = true;
                        this.reGetTimeout();
                        this.$axios({
                                method: 'get',
                                url: API.host+ '/user/sms/'+this.mobile_no
                            }).then(res=>{
                                if(res.data.code != '200'){
                                    this.errfn(res.data.msg)
                                    return;
                                }
                            });
                    }else{

                        this.errfn(res.data.msg)
                        return;
                    }
                }).catch(err=>{
                    console.log(err);
                    this.checkphone=false;
                }) 
            }
            
        },
        checkmobile(){
            let self= this;
            let phoneReg = /(^1\d{10}$)/;
            if(self.mobile_no !='' && phoneReg.test(self.mobile_no)){
                self.$axios.get(API.host+"/user/apply/check?mobile="+self.mobile_no).then(res=>{
                    console.log(res.data)
                    if(res.data.code=='200'){
                        this.checkphone=true;
                    }else{
                        this.errfn(res.data.msg)
                        return;
                    }

                }).catch(err=>{
                    console.log(err);
                    this.checkphone=false;
                }) 
            }else{
                // this.errfn('')
                return;
            }  
        },
        delimg(key){
            // 
            let self = this;
            self.imgs[key]='';
        },
        delallimg(){
            // 
            var delimg =[];
            for(var i=0;i<this.srcimg.length;i++){
              var dbimgs = utils.dbGet('img_'+this.srcimg[i]);
              
              if(dbimgs == null || dbimgs ==''){}else{
                   delimg.push(dbimgs);
              }
            }
            if(delimg.length>0){
                this.$axios.post(API.host+"/upload/del",{files:delimg}).then(res=>{
                  for(var i=0;i<this.srcimg.length;i++){
                      utils.dbRemove('img_'+this.srcimg[i])
                  }
                  console.log(res.data);
                  
                }).catch(err=>{
                    console.log(err);
                })
            }
        },
        businessData(){
            let self = this;
            self.$axios.get(API.host+'/event/type').then((res)=>{
                console.log(res);
                if(res.data.code==200){
                    let data = res.data.data.list;
                   
                    data.forEach(function(val){
                        let datas = {
                            name:'',
                            hava:false,
                            img:val.img
                        }
                        datas.name=val.detail;
                        self.business.push(datas);
                    })
                    console.log(self.business)
                }
            }).catch((err)=>{
                console.log(err);   
            })
        },
        show_others(){
            this.show_other=true;
        },
        hide_other(){
            this.show_other=false;
        },
        filterHTMLTag(msg){
          var msg = msg.replace(/<\/?[^>]*>/g, ''); //HTML Tag
          msg = msg.replace(/[|]*\n/, '') //
          msg = msg.replace(/&npsp;/ig, ''); //npsp
          msg = msg.replace(/html|www|\.com|http|https/g, ''); //npsp
          return msg;
        },
        save(){
            let self = this;
            let phoneReg = /(^1\d{10}$)/;
            let old_business =[];
            for(let i=0;i<self.business.length;i++){
                if(self.business[i].have){
                    old_business.push(self.business[i].name)
                }
            }
            if(self.others !=''){
               let txt =  self.filterHTMLTag(self.others)
                old_business.push(txt);
            }
            self.old_business = old_business;
            let postdata={
                smsCode:self.sms,
                org_name:self.full_name,
                linkman:self.linkman,
                mobile:self.mobile_no,
                wechat:self.wechat_id,
                main_business:self.old_business,
                
            }
            if(postdata.org_name==''){
                this.errfn('')
                return;
            }else if(postdata.linkman==''){
                this.errfn('')
                return;
            }else  if(postdata.mobile==''){
               this.errfn('')
                return;
            }else if(!phoneReg.test(postdata.mobile)){
                this.errfn('')
                return;
            }else if(postdata.smsCode==''){
                this.errfn('')
                return;
            }else if(postdata.smsCode.length != 6 ){
                this.errfn('6')
                return;
            }else if(postdata.main_business==''){
                this.errfn('')
                return;
            }else {
                self.reaffirm = true;
            }
        },
        again(){
            let self = this;
            self.laoding = true;
            self.reaffirm =false;
            let postdata={
                smsCode:self.sms,
                org_name:self.full_name,
                linkman:self.linkman,
                mobile:self.mobile_no,
                wechat:self.wechat_id,
                main_business:self.old_business,
                license_img:'',
            }
            if(self.imgs['license_img'] != ""){
                // 
                let imgDatas= self.imgs['license_img'];//
                let img_key = self.mobile_no+'license_img';//
                let keys = 'images/admin/orgreg/'+md5(img_key);//
                let key = imgDatas.replace(/^data:image\/\w+;base64,/, "");
              
                let base64=window.btoa(keys)
                
                let config = {
                  headers:{
                    'Content-Type':'application/octet-stream',
                    'Authorization':"UpToken "+self.token
                    }
                };  
                //
                self.$axios.post(API.qinniu+"/putb64/-1/key/"+base64,key,config)
                .then(res=>{
                    if(res.data.code='200'){
                           postdata.license_img = keys;
                           self.$axios.post(API.host+"/user/apply",postdata).then(res=>{
                                console.log(res.data)
                                if(res.data.code=='200'){
                                        this.loaidng = '!'
                                        self.org_name ='';
                                        self.linkman ='';
                                        self.mobile_no ='';
                                        self.imgs.license_img='';
                                        for(let i=0;i<self.business.length;i++){
                                            self.business[i].have=false;
                                        }
                                        self.others =''
                                        setTimeout(()=>{
                                            self.laoding = false;
                                            self.success = true;
                                        },1000)
                                }else if(res.data.code=='7'){
                                    self.laoding = false;
                                    this.success_phone =true;//
                                    this.errfn(res.data.msg);
                                    return;
                                }else{
                                    self.laoding = false;
                                    this.errfn(res.data.msg);
                                    return;
                                }

                            }).catch(err=>{
                                console.log(err);
                                this.errfn('');
                                self.laoding = false;
                            })
                    }else{
                        this.loaidng = '!'
                        self.laoding = false;
                    }
                }).catch(err=>{
                    console.log(err);
                    this.loaidng = '!'
                    self.laoding = false;
                })
            }else{
                self.$axios.post(API.host+"/user/apply",postdata).then(res=>{
                    console.log(res.data)
                        if(res.data.code=='200'){
                                this.loaidng = '!'
                                self.org_name ='';
                                self.linkman ='';
                                self.mobile_no ='';
                                self.imgs.license_img='';
                                for(let i=0;i<self.business.length;i++){
                                    self.business[i].have=false;
                                }
                                self.others =''
                                setTimeout(()=>{
                                    self.laoding = false;
                                    self.success = true;
                                },1000)
                        }else if(res.data.code=='7'){
                            self.laoding = false;
                            this.success_phone =true;//
                            this.errfn(res.data.msg);
                            return;
                        }else{
                            self.laoding = false;
                            this.errfn(res.data.msg);
                            return;
                        }

                    }).catch(err=>{
                        console.log(err);
                        this.errfn('');
                        self.laoding = false;
                    })
            }   
        },
        fileChange(key,files){
            let self =this; 
            self.upimg = true;
            let file = files[0]; 
            let imgtype = file.name.substring(file.name.lastIndexOf(".")+1).toLowerCase();
            console.log(imgtype)
            if(file.size>(self.maximg*(1024*1024))){
                this.errfn('10M')
                return;
            }else if( imgtype !="jpg" && imgtype !="png" && imgtype !="gif" && imgtype != 'jpeg' && imgtype != 'bmp' && imgtype !="webp" && imgtype !="tiff"){
                this.errfn('')
                return;
            }else{
                var reader = new FileReader();  // FileReader

                  reader.readAsDataURL(file); // fileresult base64

                  reader.onload = function(files){
                    self.imgs[key]=files.target.result;
                    console.log(files.target.result)
                  } 
                 self.upimg = false;
            }
        },
        getToken(){
            let self= this;
            // token 
            self.$axios.get(API.host+'/upload/token',{
              params:{
                  type:0,
                  delete_file:false,
              }
              }).then(res => {
                  self.token = res.data.data.uploadToken;
              });
        },
        errfn(msg){
            // 
            let self = this;
            self.tip_show = true;
            self.errtip = msg;
            setTimeout(()=>{
              this.tip_show = false;
              this.errtip = ''
            },2000) 
        },
    },
}
</script>
