    .show_m{
        display: none;
    }
    .success_box{
        position: absolute;
        width: 525px;
         min-height: 525px;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
        background-image: url('../../assets/website/images/pc/pc.png');
        background-size: 100% 100%;
        img{
            margin: 0 auto;
            margin-top: 160px;
        }
        .success_tip{
            width: 312px;
            padding-top: 14px;
            border-top: 2px solid #eeeeee;
            font-size: 18px;
            color: #6336f3;
            margin: 0 auto;
            margin-bottom: 8px;
           
        }
        .success_next{
            font-size: 22px;
            color: #6336f3
        }
        .router-link-active{
            display: block;
            margin-top: 30px;
            font-size: 16px;
            color: #898989;
        }
    }
