            border: 0;
            // padding: 0 20px;
            width: 112px;
           color: #7330e8;
           border-radius: 4px;
           border: 1px solid #7330e8;
           background-color: #fff;
        }
    }
    .err{
        margin-left: 40px;
        i{
            display: inline-block;
            width: 16px;
            height: 16px;
            vertical-align: middle;
            background-image: url('../../assets/website/images/delicon.png');
        }
        span{
            font-size: 14px;
            color: #e23737;
        }
        
    }
    
