        line-height: 40px;
        box-sizing: border-box;
        font-size: 16px;
        color: #999999;
        padding: 0 17px;
        
    }
    .active{
        border: 1px solid #672ce5;
        color: #672ce5!important;
    }
    .apply_wrap{
        width: 100%;
        height: 100vh;
        background-color: #f5f3f9;
        overflow-y: scroll;
        input{
            outline: none;
        }
        .apply_content{
            width: 100%;
            height: 100vh;
            background-color: #f5f3f9;
            .header{
                background-color: #ffffff;
                padding: (16rem/72) 0;
                .nav{
                    display: flex;
                     align-items: center;
                     width: 100%;
                     margin: 0 auto;
                      width: 1002px;
                    .logo{
                        width: (156rem/72);
                        height: (56rem/72);
                        background-image: url('../../assets/website/images/logo.png');
                        background-size: 100% 100%;
                    }
                    span{
                        height: (26rem/72);
                        line-height: (26rem/72);
                        margin-left: (12rem/72);
                        padding-left: (12rem/72);
                        font-size: (22rem/72);
                        color: #672ce5;
                        border-left: 3px solid #efe7ff;
                    }
                }
            }
            .apply_from{
                width: 100%;
                background-color: #f5f3f9;
                padding-bottom: 21px;
                .apply_boxs{
                    box-sizing: border-box;
                    padding: 65px 130px;
                    width: 1002px;
                    background-color: #fff;
                    margin: 0 auto;
                    margin-top: 43px;
                    text-align: left;
                    .business{
                        margin-bottom: 40px;
                        .err{
                            margin-left: 0;
                        }
                    }
                    h1{
                        font-size: 16px;
                        color: #333333;
                    }
                    .dj{
                        font-size: 24px;
                        margin-bottom: 61px; 
                    }
                    .yw{

                        margin-bottom: 10px;     
                    }
                    .full_name{
                        
                        
                    }
                    .business_box{
                        display: flex;
                        flex-wrap: wrap;
                        // justify-content: space-between;
                            label{
                                box-sizing: border-box;
                                padding: 2px 4px;
                                color: #999999;
                                width: 12%;
                                font-size: 14px;
                                margin-bottom: 20px;
                                text-align: center;
                                font-weight: 400;
                                display: flex;
                                align-items: center;
                                justify-content: center;
                                margin-right: 10px;
                                input{
                                    opacity: 0;
                                    display: none;
                                    width: 100%;
                                    height: 100%;
                                }
                                img{
                                    display: block;
                                    max-width: 40%;
                                    height: auto;
                                }
                            }  
                            
                            
                    }
                    .inputs{
                        position: relative;
                        box-sizing: border-box;
                        width: 180px;
                        vertical-align: middle;
                        text-align: center;
                        
                        input{
                            position: relative;
                            display: inline-block;
                            border: 0;
                            top: -1px;
                            font-size: 16px;
                            width: 160px;
                            padding: 9px 10px;
                        }
                    }
                    .submit{
                        width: 100%;
                        width: 415px;
                        box-sizing: border-box;
                        font-size: 20px;
                        color: #fff;
                        text-align: center;
                        padding: 16px 0;
                        border-radius: 5px;
                        background-color: #672ce5;
                        cursor: pointer;
                        margin-top: 30px;
                    }
                }
               
            }

            .rule_wrap{
                width: 100%;
                background-color: #f5f3f9;
                .rule_box{
                    box-sizing: border-box;
                    padding: 45px 81px;
                    width: 1002px;
                    margin: 0 auto;
                    background-color: #fff;
                    text-align: left;
                    h1{
                        color: #404040;
                        font-size: 24px;
                        padding-left: 11px;
                        border-left: 4px solid #672ce5;
                        margin-bottom: 23px;
                    }
                    h2{
                        color: #666666;
                        font-size: (18rem/72);
                        margin: (10rem/72) 0;
                    }
                    p{
                        font-size: 16px;
                        line-height: 1.75;
                        color: #666666;
                        margin-bottom: 30px;
                    }
                }
            }
            .footer{
                width: 100%;
                background-color: #f5f3f9;
                padding: 73px 0;
                font-size: 14px;
                color: #999999;
            }
        }
    }
}   


