// 
Array.prototype.remove = function(from, to) {
    var rest = this.slice((to || from) + 1 || this.length);
    this.length = from < 0 ? this.length + from : from;
    return this.push.apply(this, rest);
};
// 
Array.prototype.insert = function (index, item) {
  this.splice(index, 0, item);
};
/**
 * localStorage
 * @type {[Function]}
 */
const dbGet = (name) => {
  let value = localStorage.getItem(name)
  if (/^\{.*\}$/.test(value)) {
    value = JSON.parse(value)
  }
  return value
}

const dbSet = (name, value) => {
  if (typeof value === typeof {}) {
    value = JSON.stringify(value)
  }
  return localStorage.setItem(name, value)
}

const dbRemove = (name) => {
  return localStorage.removeItem(name)
}
const cookie = {
  set:function(key,val,time){//cookie
      var date=new Date(); //
      var expiresDays=time;  //daten
      date.setTime(date.getTime()+expiresDays*24*3600*1000); //cookie
      document.cookie=key + "=" + val +";expires="+date.toGMTString();  //cookie
  },
  get:function(key){//cookie
      /*cookie*/
      var getCookie = document.cookie.replace(/[ ]/g,"");  //cookiecookie
      var arrCookie = getCookie.split(";")  //cookie"" cookiearrCookie
      var tips;  //tips
      for(var i=0;i<arrCookie.length;i++){   //forcookietips
          var arr=arrCookie[i].split("=");   //cookie""cookiearr
          if(key==arr[0]){  //arr[0]cookietips
              tips=arr[1];   //cookietips
              break;   //for
          }
      }
      return tips;
  },
  delete:function(key){ //cookie
      var date = new Date(); //
      date.setTime(date.getTime()-10000); //date
      document.cookie = key + "=v; expires =" +date.toGMTString();//cookie
  }
}
/**
 * sessionStorage
 * @type {[Function]}
 */
const ssdbGet = (name) => {
  let value = sessionStorage.getItem(name)
  if (/^\{.*\}$/.test(value)) {
    value = JSON.parse(value)
  }
  return value
}

const ssdbSet = (name, value) => {
  if (typeof value === typeof {}) {
    value = JSON.stringify(value)
  }
  return sessionStorage.setItem(name, value)
}

const ssdbRemove = (name) => {
  return sessionStorage.removeItem(name)
}

const urlSearch = () => {
   var name,value, obj={};
   var str=location.href; //
   var num=str.indexOf("?")
   str=str.substr(num+1); //   stringvar.substr(start [, length ]
   if(str.indexOf('#')>-1){
    str = str.substr(0,str.indexOf('#'))
   }
   var arr=str.split("&"); //
   for(var i=0;i < arr.length;i++){
    num=arr[i].indexOf("=");
    if(num>0){
     name=arr[i].substring(0,num);
     value=arr[i].substr(num+1);
     obj[name]=value;
     }
    }
    return obj
}
// 
const toThousands = (num) => {
  var num = (num || 0).toString(), result = '';
  while (num.length > 3) {
      result = ',' + num.slice(-3) + result;
      num = num.slice(0, num.length - 3);
  }
  if (num) { result = num + result; }
  return result;
}
// 
const IdCodeValid = function(code){
    //
    //1518
    //
    var city={11:"",12:"",13:"",14:"",15:"",21:"",22:"",23:" ",31:"",32:"",33:"",34:"",35:"",36:"",37:"",41:"",42:" ",43:"",44:"",45:"",46:"",50:"",51:"",52:"",53:"",54:" ",61:"",62:"",63:"",64:"",65:"",71:"",81:"",82:"",91:" "};
    var row={
        'pass':true,
        'msg':''
    };
    if(!code || !/^\d{6}(18|19|20)?\d{2}(0[1-9]|1[012])(0[1-9]|[12]\d|3[01])\d{3}(\d|[xX])$/.test(code)){
        row={
            'pass':false,
            'msg':''
        };
    }else if(!city[code.substr(0,2)]){
        row={
            'pass':false,
            'msg':''
        };
    }else{
        //18
        if(code.length == 18){
            code = code.split('');
            //(aiWi)(mod 11)
            //
            var factor = [ 7, 9, 10, 5, 8, 4, 2, 1, 6, 3, 7, 9, 10, 5, 8, 4, 2 ];
            //
            var parity = [ 1, 0, 'X', 9, 8, 7, 6, 5, 4, 3, 2 ];
            var sum = 0;
            var ai = 0;
            var wi = 0;
            for (var i = 0; i < 17; i++)
            {
                ai = code[i];
                wi = factor[i];
                sum += ai * wi;
            }
            if(parity[sum % 11] != code[17].toUpperCase()){
                row={
                    'pass':false,
                    'msg':''
                };
            }
        }
    }
    return row;
};
export default {
  dbGet,
  dbSet,
  dbRemove,
  ssdbGet,
  ssdbSet,
  ssdbRemove,
  urlSearch,
  toThousands,
  IdCodeValid,
  cookie
}



